/* ServiceFilters.css - Enhanced design */
.service-filters {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }
  
  .filters-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
  }
  
  .filters-header h3 {
    font-size: 1.2rem;
    font-weight: 600;
    color: #0f172a;
    margin: 0;
  }
  
  .clear-filters-btn {
    background: none;
    border: none;
    color: #0284c7;
    font-size: 0.85rem;
    font-weight: 500;
    cursor: pointer;
    padding: 4px 8px;
    border-radius: 4px;
    transition: background-color 0.2s;
  }
  
  .clear-filters-btn:hover {
    background-color: #e0f2fe;
  }
  
  .filter-section {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }
  
  .filter-section-title {
    font-size: 0.9rem;
    font-weight: 600;
    color: #475569;
    margin: 0;
  }
  
  .filter-group {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }
  
  /* Radio buttons styling */
  .radio-group {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }
  
  .radio-label {
    display: flex;
    align-items: center;
    cursor: pointer;
    position: relative;
    padding-left: 28px;
  }
  
  .radio-label input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }
  
  .custom-radio {
    position: absolute;
    top: 0;
    left: 0;
    height: 18px;
    width: 18px;
    background-color: #f1f5f9;
    border: 2px solid #cbd5e1;
    border-radius: 50%;
    transition: all 0.2s;
  }
  
  .radio-label:hover .custom-radio {
    background-color: #e2e8f0;
  }
  
  .radio-label input:checked ~ .custom-radio {
    background-color: #0284c7;
    border-color: #0284c7;
  }
  
  .custom-radio:after {
    content: "";
    position: absolute;
    display: none;
    top: 4px;
    left: 4px;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background: white;
  }
  
  .radio-label input:checked ~ .custom-radio:after {
    display: block;
  }
  
  .radio-text {
    font-size: 0.95rem;
    color: #334155;
  }
  
  /* Date input styling */
  .date-input {
    width: 100%;
    padding: 10px 12px;
    border: 1px solid #cbd5e1;
    border-radius: 8px;
    font-size: 0.95rem;
    color: #0f172a;
    background-color: #f8fafc;
    transition: border-color 0.2s, box-shadow 0.2s;
  }
  
  .date-input:focus {
    outline: none;
    border-color: #60a5fa;
    box-shadow: 0 0 0 3px rgba(96, 165, 250, 0.2);
  }
  
  /* Search input styling */
  .search-input-wrapper {
    position: relative;
  }
  
  .search-input {
    width: 100%;
    padding: 10px 12px;
    padding-left: 34px;
    border: 1px solid #cbd5e1;
    border-radius: 8px;
    font-size: 0.95rem;
    color: #0f172a;
    background-color: #f8fafc;
    transition: border-color 0.2s, box-shadow 0.2s;
  }
  
  .search-input:focus {
    outline: none;
    border-color: #60a5fa;
    box-shadow: 0 0 0 3px rgba(96, 165, 250, 0.2);
  }
  
  .search-icon {
    position: absolute;
    left: 12px;
    top: 50%;
    transform: translateY(-50%);
    color: #94a3b8;
    font-size: 0.9rem;
  }
  
  /* Active filters section */
  .active-filters {
    margin-top: 8px;
  }
  
  .filter-tags {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-top: 12px;
  }
  
  .filter-tag {
    background-color: #e0f2fe;
    color: #0369a1;
    padding: 8px 12px;
    border-radius: 6px;
    font-size: 0.85rem;
  }
  
  .tag-value {
    font-weight: 600;
    text-transform: capitalize;
  }
  
  .no-filters {
    color: #94a3b8;
    font-size: 0.85rem;
    font-style: italic;
  }
  
  /* Mobile adjustments */
  @media (max-width: 768px) {
    .service-filters {
      padding-bottom: 24px;
    }
    
    .filter-section-title {
      font-size: 0.85rem;
    }
  }