/* BookingForm.css - Enhanced design */
.booking-modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(4px);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    padding: 20px;
  }
  
  .booking-form-container {
    background-color: white;
    border-radius: 12px;
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
    width: 100%;
    max-width: 540px;
    overflow: hidden;
    animation: slideIn 0.3s ease-out;
  }
  
  @keyframes slideIn {
    from {
      transform: translateY(30px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
  
  .booking-header {
    background-color: #0369a1;
    background-image: linear-gradient(135deg, #0369a1, #0c4a6e);
    color: white;
    padding: 20px 24px;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .booking-header h2 {
    margin: 0;
    font-size: 1.4rem;
    font-weight: 600;
  }
  
  .close-button {
    background: none;
    border: none;
    color: white;
    font-size: 1.8rem;
    cursor: pointer;
    height: 30px;
    width: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    transition: background-color 0.2s;
  }
  
  .close-button:hover {
    background-color: rgba(255, 255, 255, 0.2);
  }
  
  .service-details {
    padding: 24px;
    border-bottom: 1px solid #e5e7eb;
    background-color: #f8fafc;
  }
  
  .detail-group {
    margin-bottom: 14px;
    display: flex;
  }
  
  .detail-label {
    font-weight: 600;
    color: #475569;
    min-width: 80px;
    flex-shrink: 0;
  }
  
  .detail-value {
    color: #0f172a;
    flex-grow: 1;
  }
  
  .package-id {
    margin-top: 16px;
    font-size: 0.85rem;
    color: #64748b;
    padding-top: 12px;
    border-top: 1px dashed #e2e8f0;
  }
  
  .package-id span {
    font-weight: 500;
  }
  
  .booking-form {
    padding: 24px;
  }
  
  .form-title {
    font-weight: 600;
    font-size: 1.1rem;
    margin-bottom: 20px;
    color: #0f172a;
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  .form-group label {
    display: block;
    margin-bottom: 8px;
    font-weight: 500;
    color: #475569;
  }
  
  .form-group input {
    width: 100%;
    padding: 12px;
    border: 1px solid #d1d5db;
    border-radius: 8px;
    font-size: 1rem;
    transition: border-color 0.2s;
  }
  
  .form-group input:focus {
    outline: none;
    border-color: #60a5fa;
    box-shadow: 0 0 0 3px rgba(96, 165, 250, 0.2);
  }
  
  .form-group input.error-input {
    border-color: #ef4444;
  }
  
  .form-error-message {
    color: #ef4444;
    padding: 12px;
    background-color: #fef2f2;
    border-radius: 8px;
    margin-bottom: 20px;
    font-size: 0.9rem;
    border-left: 4px solid #ef4444;
  }
  
  .form-actions {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
  }
  
  .cancel-button {
    background-color: #f1f5f9;
    border: 1px solid #cbd5e1;
    border-radius: 8px;
    padding: 12px 20px;
    cursor: pointer;
    font-weight: 500;
    color: #475569;
    transition: background-color 0.2s;
  }
  
  .cancel-button:hover {
    background-color: #e2e8f0;
  }
  
  .submit-button {
    background-color: #0284c7;
    color: white;
    border: none;
    border-radius: 8px;
    padding: 12px 24px;
    font-weight: 600;
    cursor: pointer;
    transition: background-color 0.2s;
    display: flex;
    align-items: center;
  }
  
  .submit-button:hover {
    background-color: #0369a1;
  }
  
  .submit-button:disabled,
  .cancel-button:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
  
  /* Success screen styles */
  .booking-success-container {
    background-color: white;
    border-radius: 12px;
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
    width: 100%;
    max-width: 500px;
    padding: 30px;
    text-align: center;
    animation: slideIn 0.3s ease-out;
  }
  
  .success-icon {
    background-color: #10b981;
    color: white;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 26px;
    margin: 0 auto 20px;
  }
  
  .booking-success-container h3 {
    color: #10b981;
    margin-bottom: 15px;
    font-size: 1.5rem;
  }
  
  .customer-name {
    font-weight: 600;
    margin-bottom: 20px;
    font-size: 1.1rem;
  }
  
  .service-summary {
    background-color: #f8fafc;
    border-radius: 8px;
    padding: 16px;
    margin-top: 20px;
    text-align: left;
  }
  
  .summary-item {
    margin-bottom: 10px;
  }
  
  .summary-label {
    font-weight: 500;
    color: #475569;
    margin-right: 8px;
  }
  
  .summary-value {
    color: #0f172a;
  }
  
  .button-spinner {
    display: inline-block;
    width: 16px;
    height: 16px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    border-top-color: white;
    animation: spin 1s ease-in-out infinite;
    margin-right: 8px;
  }
  
  @keyframes spin {
    to { transform: rotate(360deg); }
  }
  
  /* Responsive adjustments */
  @media (max-width: 576px) {
    .booking-form-container,
    .booking-success-container {
      max-width: 100%;
      border-radius: 8px;
    }
    
    .form-actions {
      flex-direction: column-reverse;
      gap: 12px;
    }
    
    .cancel-button,
    .submit-button {
      width: 100%;
      justify-content: center;
    }
  }